@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.edgtf-comment-holder {
    @include edgtfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .edgtf-comment-holder-inner {
        padding: 27px 0 20px;
    }

    .edgtf-comment {

        &.edgtf-pingback-comment {

            .edgtf-comment-text {
                padding: 0;
            }
        }
    }



    .edgtf-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include edgtfRelativeHolderLayout();
            margin: 32px 0 0;
            padding: 32px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 132px;
        }
    }

    .edgtf-comments-title {
        margin-bottom: 32px;

        > * {
            margin: 0;
        }
    }

    .edgtf-comment-image {
        position: relative;
        top: 5px;
        left: 0;
        width: 100px;
        height: 100px;
        display: block;
        float: left;

        img {
            display: block;
        }
    }

    .edgtf-comment-text {
        padding: 0 0 0 132px;

        .edgtf-comment-info {
            @include edgtfRelativeHolderLayout();
            margin: 7px 0 4px;
        }

        .edgtf-text-holder{
            margin: 0 0 13px;

            p{
                font-size: 15px;
                line-height: 26px;
            }
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            margin-right: 10px;
            font-size: 15px;
            font-weight: 600;
            letter-spacing: 0.1em;
            color: $default-heading-color;
            font-family: $additional-heading-font;
            text-transform: uppercase;

            &:hover {
                color: $first-main-color;
            }
        }

        .edgtf-comment-name {
            float: left;
            margin: 0;
        }

        .edgtf-comment-date {
            float: left;
            margin: 0;
            padding: 2px 0 0;
            @include edgtfH6Aux();
        }

        .edgtf-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.edgtf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .edgtf-comment-form-inner {
        padding: 58px 0 78px;
    }

    .comment-reply-title {
        margin: 0 0 32px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }

    .comment-form-cookies-consent{
        margin: 32px 0 10px;

        input{
            margin: 3px 10px 3px 4px;

            .edgtf-safari &{
                position: relative;
                top: -2px;
            }
        }
    }
}

@include ipad-portrait {

    .edgtf-comment-holder {

        .edgtf-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .edgtf-comment-holder {

        .edgtf-comment-list {

            .children {
                padding: 0;
            }
        }

        .edgtf-comment-text {

            .edgtf-comment-date,
            .edgtf-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .edgtf-comment-holder {

        .edgtf-comment-image {
            float: none;
        }

        .edgtf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.edgtf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }

    &.edgtf-btn{
        margin-top: 25px;

        &.edgtf-button-dark.edgtf-btn-custom-border-hover.edgtf-btn-custom-hover-color.edgtf-btn-custom-hover-bg{
            &:hover{
                animation: edgtfBtnHover 1.2s ease;
            }
        }

        &.edgtf-button-light.edgtf-btn-custom-border-hover.edgtf-btn-custom-hover-color.edgtf-btn-custom-hover-bg{
            &:hover{
                animation: edgtfBtnLightHover 1.2s ease;
            }
        }

        @keyframes edgtfBtnLightHover {
            0% {
                background-color: #fff;
                border-color: #fff;
            }

            30%, 33% {
                background-color: #696969;
                border-color: #696969;
            }

            100% {
                background-color: #fff;
                border-color: #fff;
            }
        }

        footer & {
            margin-top: 0;
        }
    }

    &.wpcf7-textarea{
        height: 184px;
    }
}

.edgtf-btn{
    margin-top: 25px;

    &.edgtf-button-dark.edgtf-btn-custom-border-hover.edgtf-btn-custom-hover-color.edgtf-btn-custom-hover-bg{
        &:hover{
            animation: edgtfBtnHover 1.2s ease;
        }
    }

    &.edgtf-button-light.edgtf-btn-custom-border-hover.edgtf-btn-custom-hover-color.edgtf-btn-custom-hover-bg{
        &:hover{
            animation: edgtfBtnLightHover 1.2s ease;
        }
    }
}

.wpcf7-form.invalid{
    .edgtf-cf-textarea-holder{
        overflow: visible !important;
    }
}

div.wpcf7-response-output{
    margin-left: 0;
    margin-right: 0;
    margin-top: 25px;
    border-color: #f00;
    color: #f00;
}

.edgtf-galatia-cf{
    .edgtf-grid-row {
        .edgtf-grid-col-6:nth-child(2n) {
            .wpcf7-form-control {
                border-left-width: 0;

                @include ipad-landscape{
                    border-left-width: 1px;
                }
            }
        }
    }
}

//#submit_comment, // added our button classes in comments.php
//input.wpcf7-form-control.wpcf7-submit, // added our button classes in contact-form-7-confing.php
.post-password-form input[type='submit']{
    @include edgtfButtonDefaultStyle();
    @include edgtfButtonSize();
    @include edgtfButtonSolidColor();
    margin-top: 20px;
    cursor: pointer;

    &:hover {
        animation: edgtfBtnHover 1.2s ease;
    }
}

p.form-submit,
.wpcf7-form p {
    margin: 33px 0 0;
}

#respond textarea,
.edgtf-style-form textarea,
.wpcf7-form textarea {
    display: block;
    overflow: auto;//ie scrollbar remove
}

.edgtf-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.edgtf-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.edgtf-slider-is-moving {
        .owl-item {
            .edgtf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.edgtf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 24px); // 21 is is half height of pagination control + top margin
                transform: translateY($pos);
            }
        }
    }

    &.edgtf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: #fff;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;
            transform: translateY(-50%);
            transition: color .2s ease-out;

            svg{
                line{
                    transition: stroke 0.2s ease-out;
                    stroke: currentColor;
                }
            }

            &:hover {
                color: $first-main-color;
            }

            > span {
                display: inline-block;
                vertical-align: middle;
                font-size: 46px;
                line-height: 1;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 30px;
        }

        .owl-next {
            right: 30px;
        }
    }

    .owl-dots{
        background-color: transparent;
        text-align: center;
        margin: 16px 0 0;

        .owl-dot{
            display: inline-block;
            vertical-align: middle;
            padding: 10px 5px;
            box-sizing: border-box;
            margin: 0 4px;
            background: transparent;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            counter-increment: testimonial-counter;
            @include edgtfH6Aux();
            letter-spacing: 0;
            overflow: visible;
            transition: all 0.2s ease;

            &:before{
                content: counter(testimonial-counter,decimal);
                color:#000;
                background-color: transparent;
                padding: 0;
                transition: all 0.2s ease;
            }

            span{
                display: none;
            }

            &:hover,
            &.active{
                background-color: #000;

                &:before {
                    color: #fff;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .edgtf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .edgtf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                transition: border-color 0.2s ease-out;
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.edgtf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .edgtf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .edgtf-wrapper {
        padding: 0 !important;
    }

    .edgtf-content {
        height: 100vh;
    }

    &.admin-bar {
        .edgtf-content {
            height: calc(100vh - 32px);
        }
    }

    .edgtf-content-inner {
        @include edgtfTableLayout();
    }

    .edgtf-page-not-found {
        @include edgtfTableCellLayout();

        padding: 0 20%;
        text-align: center;

        .edgtf-404-title {
            margin: 0;
        }

        .edgtf-404-subtitle {
            margin: 35px 0 0;
        }

        .edgtf-404-text {
            margin: 10px 0 0;
        }

        .edgtf-btn {
            margin: 30px 0 0;

            &.edgtf-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-landscape {
        .edgtf-content {
            min-height: 100vh;
            padding-top: 45px;
        }

        &.admin-bar {
            .edgtf-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }
    }

    @include ipad-portrait {
        .edgtf-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#edgtf-back-to-top {
    position: fixed;
    width: 46px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;

        transition: opacity 0.15s ease, visibility .0s .15s;
    }

    &.on {
        opacity: 1;
        visibility: visible;

        transition: opacity 0.3s ease;
    }

    span {
        position: relative;
        display: block;
        width: 46px;
        height: 46px;
        line-height: 46px;
        text-align: center;
        background-color: $first-main-color;
        border: 1px solid $first-main-color;
        overflow: hidden;

        transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;

        .edgtf-back-to-top-svg{
            display: block;
            width: 100%;
            height: 100%;
            fill: #fff;
            transform: translateY(21px);
            transition: all 0.3s ease;
        }

        &:hover {
            .edgtf-back-to-top-svg{
                transform: translateY(8px);
            }
        }
    }

}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.edgtf-self-hosted-video-holder {
    .edgtf-video-wrap {
        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

