   #Main SVG Arrow end
   ========================================================================== */


.vc_row{
    &.edgtf-row-with-in-grid-bg-color{
        position: relative;

        .edgtf-in-grid-bg-color-holder{
            @include edgtfAbsoluteHolderLayout();
            z-index: -1;

            .edgtf-container-inner{
                height: 100%;
            }
        }

    }
}

.edgtf-right-side-menu-opened {

    cursor: url('../img/sidearea-cursor.png'), auto !important;
    
    &.edgtf-ms-explorer{
        cursor: auto !important;
    }

    .touch &{
        cursor: auto !important;
    }

}

.edgtf-row-grid-section-wrapper.edgtf-with-uncover-section{
    visibility:hidden;

    .touch & {
        visibility: visible;
    }

    .no-touch & {
        visibility: visible;
        -webkit-clip-path: inset(0 100% 0 0);
        clip-path: inset(0 100% 0 0);

        .edgtf-eh-item-inner {
            opacity: 0;
            transform: translate3d(0, 70px, 0);
        }

        &.edgtf-uncover {
            -webkit-clip-path: inset(0 0 0 0);
            clip-path: inset(0 0 0 0);
            transition: all 1s cubic-bezier(0.86, 0, 0.07, 1);

            .edgtf-eh-item {
                .edgtf-eh-item-inner {
                    opacity: 1;
                    transform: translate3d(0, 0, 0);
                }

                &:first-child {
                    .edgtf-eh-item-inner {
                        transition: all 1s cubic-bezier(0.23, 1, 0.32, 1) .6s;
                    }
                }

                &:last-child {
                    .edgtf-eh-item-inner {
                        transition: all 1s cubic-bezier(0.23, 1, 0.32, 1) .9s;
                    }
                }
            }
        }
    }
}

