/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 $widget-bottom-margin;
    padding: 0;

    .edgtf-widget-title-holder {
        margin-bottom: $widget-title-margin;

        .edgtf-widget-title {
            color: $widget-heading-color;
            margin-bottom: 3px;
        }

        .edgtf-separator {
            border-bottom-width: 3px;
            border-color: $widget-text-color;
            width: 30px;
        }
    }

    ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            border-bottom-color: $widget-list-item-border-color;
            border-bottom-style: $widget-list-item-border-style;
            border-bottom-width: $widget-list-item-border-width;
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: ($widget-list-item-padding + 6px) 0 ($widget-list-item-padding + 1px);

            &:last-child {
                border: 0;
                padding-bottom: 0;
            }

            a {
                color: $widget-link-color;
                font-size: 17px;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            padding: 5px 0 15px 17px;
            box-sizing: border-box;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and animation on link hover

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu {
        .edgtf-widget-title {
            margin-bottom: 0;
        }

        ul {
            li {
                overflow: hidden;

                .edgtf-ms-explorer &,
                .edgtf-edge &{
                    overflow: visible;
                }

                a {
                    display: inline-block;
                    vertical-align: middle;
                    overflow: hidden;
                    padding: 0 15px 0 0;
                    position: relative;
                    transition: padding .2s ease;

                    .edgtf-ms-explorer &,
                    .edgtf-edge &{
                        overflow: visible;
                    }
                }
            }
        }
    }
	
	&.widget_archive,
	&.widget_categories {
		ul li a {
			padding-right: 0;
		}
	}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and w/o animation on link hover

    &.widget_recent_comments,
    &.widget_rss {
        .edgtf-widget-title {
            margin-bottom: 0;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // calendar

    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            background-color: $widget-list-item-background-color;
            color: $widget-text-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }

        td{
            &#prev{
                text-align: left;
            }

            &#next{
                text-align: right;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // rss

    &.widget_rss {

        ul{
            li{
                padding-bottom: 14px;

                &:last-child{
                    padding-bottom: 0;
                }

                .rss-widget-icon {
                    display: inline-block;
                }

                .rss-date {
                    display: block;
                    margin: 5px 0 8px;
                }

                cite {
                    display: block;
                    margin-top: calc(#{$widget-list-item-padding} / 2);
                }

                .rsswidget{
                    @include edgtfH6Aux();
                }

                .rssSummary{
                    margin: 8px 0;
                }
            }
        }
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // search

    .edgtf-searchform {
        .input-holder {
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: 1px;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $widget-text-color;
                display: inline-block;
                float: left;
                font-family: $default-text-font;
                font-size: 17px;
                font-weight: inherit;
                height: $widget-search-form-height;
                line-height: calc(#{$widget-search-form-height} - 2px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;
                transition: color .2s ease-in-out;
            }

            input.search-field {
                padding: 0 0 0 21px;
                width: calc(100% - 65px);

                @include edgtfPlaceholder {
                    color: $widget-text-color;
                }
            }

            button {
                background: $widget-list-item-background-color;
                color: $widget-text-color;
                cursor: pointer;
                font-size: 23px;
                width: 44px;
                transition: color .2s ease-in-out;
                padding: 1px 5px 1px 24px;

                &:hover {
                    color: $widget-link-hover-color;
                }

                span {
                    display: block;
                    left: 0;
                    line-height: inherit;
                    position: absolute;
                    top: 0;
                    width: 100%;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // combobox

    //&.widget_archive, // to affect dropdown in woo widgets
    //&.widget_categories,
    //&.widget_text {
        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-radius: 0;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;
            transition: color .2s ease-in-out;
        }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // tag cloud

    .tagcloud {
        a {
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 17px !important;
            font-weight: inherit;
            line-height: $widget-tag-cloud-height;
            margin: 0;
            padding: 0;

            &:hover {
                color: $widget-link-hover-color;
            }

            &:after{
                content: ',';
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // text

    &.widget_text {
        img {
            display: block;
        }

        p {
            &:first-child {
                margin-top: 0;
                padding-top: $widget-list-item-padding;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}