.edgtf-page-header,
.edgtf-top-bar,
.edgtf-sticky-header,
.edgtf-mobile-header {
    .widget {
        display: inline-block;
        vertical-align: middle;
        margin: 0;
    }

    // prevent monster widget from displaying in our horizontal widget areas
    div[id^=monster-widget-placeholder] {
        display: none !important;
    }
}