aside.edgtf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $default-text-color;

        &:last-child {
            margin: 0 !important;
        }
    }
}

.wpb_widgetised_column {
    position: relative;

}

aside.edgtf-sidebar,
.wpb_widgetised_column {
    // widgets spacing
    $widget-bottom-margin: 39px;
    $widget-bottom-padding: 15px;
    $widget-inner-padding: 15px;

    // widget color
    $widget-background-color: #eeeeee;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 2px;

    // widget title bottom margin
    $widget-title-margin: 14px;

    // list items color
    $widget-list-item-background-color: #ffffff;

    // list items border
    $widget-list-item-border-color: $default-border-color;
    $widget-list-item-border-width: 0px;
    $widget-list-item-border-style: solid;

    // text
    $widget-text-color: inherit;
    $widget-heading-color: inherit;

    // links
    $widget-link-color: $default-text-color;
    $widget-link-hover-color: $first-main-color;

    // specific widgets
    $widget-search-form-height: $input-height;
    $widget-tag-cloud-height: 29px;

    @import 'widgets/default-wordpress-widgets-flat';
    @import 'widgets/standalone-widgets';

    .widget_categories{
        ul{
            li{
                &:first-child{
                    padding-top: 4px;
                }
            }
        }
    }

    .widget{
        select{
            border-width: 1px;
        }

        .tagcloud {

            margin-top: 25px;

            a {
                color: #fff;
                display: inline-block;
                font-family: $additional-heading-font;
                font-size: 15px !important;
                letter-spacing: 0.1em;
                font-weight: 700;
                text-transform: uppercase;
                line-height: 35px;
                margin: 0 0 5px;
                padding: 0 19px;
                background-color: #000;
                
                &:after{
                    display: none; 
                }

                &:hover {
                    color: #fff;
                    animation: edgtfBtnHover 1.2s;
                }
            }
        }
    }
}